import { Component, h, Prop, Method } from '@stencil/core';
import InputInterface from './inputInterface';
@Component({
      tag: 'ks-input-select'
})
export class InputSelect implements InputInterface {

    @Prop() name: string;
    @Prop() label: string;

    @Prop() error: boolean;
    @Prop() disabled: boolean;
    @Prop({mutable: true}) lightUp: boolean = false;

	render() {
        const lightUp = this.lightUp ? {
            borderColor: "#1488ff",
            backgroundColor: "#d5e6ff",
            color: "#004454"
        } : {}

		return(
            <div class="uk-margin">
                {this.label ? 
                    <label class={"uk-form-label" + (this.error ? " uk-text-danger" : "")} style={{marginBottom: "3px", display: "block"}}>{this.label}</label>
                : null}

			    <select class={"uk-select" + (this.error ? " uk-form-danger" : "")} style={lightUp} name={this.name} onChange={()=>this.Validate()} disabled={this.disabled}>
                    <slot/>
                </select>
            </div>
		);
    }

    @Method()
    async IsValid() {
        return true;
    }

    @Method()
    async Validate() {
        this.lightUp = false;
        return Promise.resolve();
    }
}
